import * as Yup from "yup";

const phoneRegExp = /^\+7\([0-9]{3}\) [0-9]{3}-[0-9]{2}-[0-9]{2}$/;
const passportRegExp = /^[0-9]{2} [0-9]{2} [0-9]{6}$/;

export const SignupSchema = Yup.object().shape({
  fio: Yup.string().required("  "),
  passport: Yup.string()
    .typeError("     ")
    .required("     ")
    .matches(passportRegExp, "  "),
  contract_number: Yup.number(),
  phone: Yup.string()
    .required("   ")
    .matches(phoneRegExp, "  "),
  email: Yup.string()
    .email("  email")
    .required("    "),
  birthday: Yup.date()
    .nullable()
    .required("   ")
    .typeError("  "),
  amount: Yup.string().required("  "),
});
